@use "../abstracts" as *;

//CONTAINS ALL BASE CODE
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,400&family=Cabin:wght@100;300;400;500;600;700;900&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
body {
  font-family: $main-text-stack;
  font-weight: 300;
}

::selection {
  background-color: $primary-color;
  color: $secondary-color;
}

a{
  text-decoration: none;
}